import React from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { signupUser } from 'redux/auth/authOperations';
import { useFormik } from 'formik';
import 'react-datepicker/dist/react-datepicker.css';
import { format } from 'date-fns';
import * as yup from 'yup';
import sprite from 'assets/sprite.svg';
import {
  RegistrationContainer,
  Title,
  StyledInput,
  StyledDatePicker,
  IconСalendar,
  StyledBtn,
} from './AuthForm.styled';

const validationSchema = yup.object().shape({
  name: yup
    .string()
    .min(2, ({ min }) => `Name must be at least ${min} characters`)
    .max(16, ({ max }) => `Name must be at least ${max} characters`)
    .required('Name is required')
    .label('Name'),
  birthdate: yup.date().nullable().required('Your age is required'),
  email: yup
    .string()
    .email('Please enter valid email')
    .required('Email address is Required'),
  password: yup
    .string()
    .min(8, ({ min }) => `Password must be at least ${min} characters`)
    .max(30, ({ max }) => `Password must be no more than ${max} characters`)
    .required('Password is required'),
});

const formatDate = date => {
  return format(date, 'dd/MM/yyyy');
};

const AuthForm = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const initialValues = {
    name: '',
    birthdate: null,
    email: '',
    password: '',
  };

  const resetForm = () => {
    formik.resetForm({
      values: initialValues,
    });
  };

  const onSubmit = values => {
    const formattedValues = {
      ...values,
      birthdate: values.birthdate ? formatDate(values.birthdate) : null,
    };

    dispatch(signupUser({ ...formattedValues }));
    resetForm();
    navigate('/home', { replace: true });
  };

  const formik = useFormik({
    initialValues,
    onSubmit,
    validationSchema,
  });

  return (
    <RegistrationContainer onSubmit={formik.handleSubmit}>
      <Title>Sign up</Title>
      <div>
        <label htmlFor="name">
          <StyledInput
            name="name"
            type="text"
            placeholder="Name"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            value={formik.values.name}
          />
        </label>
        {formik.touched.name && formik.errors.name && (
          <div style={{ color: 'red' }}>{formik.errors.name}</div>
        )}
      </div>
      <div>
        <label htmlFor="birthdate">
          <StyledDatePicker
            name="birthdate"
            placeholderText="dd/MM/yyyy"
            selected={formik.values.birthdate}
            onChange={date => {
              formik.setFieldValue('birthdate', date);
            }}
            dateFormat="dd/MM/yyyy"
            onBlur={formik.handleBlur}
          />

          <IconСalendar>
            <use href={sprite + '#icon-calendar'} />
          </IconСalendar>
        </label>
        {formik.touched.birthdate && formik.errors.birthdate && (
          <div style={{ color: 'red' }}>{formik.errors.birthdate}</div>
        )}
      </div>
      <div>
        <label htmlFor="email">
          <StyledInput
            name="email"
            type="email"
            placeholder="Email"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            value={formik.values.email}
          />
        </label>
        {formik.touched.email && formik.errors.email && (
          <div style={{ color: 'red' }}>{formik.errors.email}</div>
        )}
      </div>
      <div>
        <label htmlFor="password">
          <StyledInput
            name="password"
            type="password"
            placeholder="Password"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            value={formik.values.password}
          />
        </label>
        {formik.touched.password && formik.errors.password && (
          <div style={{ color: 'red' }}>{formik.errors.password}</div>
        )}
      </div>
      <StyledBtn
        type="submit"
        disabled={!formik.isValid || formik.isSubmitting}
      >
        Sign up
      </StyledBtn>
    </RegistrationContainer>
  );
};

export default AuthForm;
